掌握，也正是這種神奇的多變性，吸引了自古至今無數的名人巨匠在追求墨韻、變化的方面付諸了不懈的探索，至今未間斷。")])]),_c('v-spacer'),_c('div',{staticClass:"col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12 t1",staticStyle:{"height":"70%","overflow":"hidden"}},[_c('img',{attrs:{"src":require("../assets/zhi-1.jpg")}})])],1),_c('div',{staticClass:"row",staticStyle:{"margin-top":"200px","height":"500px"}},[_c('div',{staticClass:"col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12 t2",staticStyle:{"height":"70%","overflow":"hidden"}},[_c('img',{attrs:{"src":require("../assets/zhi-2.jpg")}})]),_c('v-spacer'),_c('div',{staticClass:"col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12 text-center text-sm-left t2"},[_c('h3',[_vm._v("熟宣")]),_c('h4'),_c('p',{staticClass:"mt-5"},[_vm._v("熟宣是加工時用明礬等塗過，故紙質較生宣為硬，吸水能力弱，使得使用時墨和色不會洇散開來。因此特性，使得熟宣宜於繪工筆畫而非水墨寫意畫。其缺點是久藏會出現「漏礬」或脆裂。熟宣可再加工，珊瑚、雲母箋、冷金、灑金、蠟生金花羅紋、桃紅虎皮等皆為由熟宣再加工的花色紙。生宣則吸水力強。用淡墨水寫時，墨水容易滲入，化開。用濃墨水寫則相對容易。故創作書畫時，需要掌握好墨的濃淡程度，方可得心應手。")])])],1),_c('div',{staticClass:"row",staticStyle:{"margin-top":"200px","height":"500px"}},[_c('div',{staticClass:"col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12 text-center text-sm-left t3"},[_c('h3',[_vm._v("毛邊紙")]),_c('h4'),_c('p',{staticClass:"mt-5"},[_vm._v("初學者使用毛邊紙進行練習即可，沒有必要用太好的宣紙。等以後能開始寫作品了，就需要選好一點的宣紙了。")]),_c('p',[_vm._v("毛邊紙，古代漢族勞動人民用竹纖維製成的淡黃紙。明末江西出產竹紙，紙質細膩，薄而鬆軟，呈淡黃色，沒有抗水性能，托墨吸水性能好，既適於寫字，又可用於印製古籍。因明代大藏書家毛晉嗜書如命，好用竹紙印刷書籍，曾到江西大量訂購稍厚實的竹紙，並在紙邊上蓋一個篆書「毛」字印章，故人們習慣稱這種紙為毛邊紙，並沿用至今。")])]),_c('v-spacer'),_c('div',{staticClass:"col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12 t3",staticStyle:{"height":"70%","overflow":"hidden"}},[_c('img',{attrs:{"src":require("../assets/zhi-3.jpg")}})])],1),_c('h1',{staticClass:"mt-16 t5"},[_vm._v("如何挑選宣紙")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"mx-auto col-sm-12 col-md-8 mx-auto col-lg-5 col-xl-5 col-12"},[_c('p',{staticClass:"font-weight-black text-center"},[_vm._v("薄紙宜畫、厚紙宜書；單宣宜畫、夾宣宜書。但不絕對，還有個人喜好。")]),_c('p',{staticClass:"mt-5"},[_vm._v("1. 眼看——好紙不一定白，太白說明增白劑太多，不利久藏；紙白但不刺眼，反光柔和；不能有草梗、沙粒、裂口、洞眼、其他附著物。")])